.cart {
  .woocommerce-notices-wrapper {
    padding-top: 20px;
  }
  .woocommerce-message {
    border-top-color: $color-default;

    &:before {
      color: $color-default;
    }
  }
}
.cart-all {
  padding-top: 27px;
  display: flex;
  justify-content: space-between;
  transition: .3s;
  .cart-product {
    width: 100%;
    &-item {
      border-bottom: 1px solid $color-gray;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 30px 20px 30px 0;
      &:first-child {
        border-top: 1px solid $color-gray;
      }
    }
  }
  &-info {
    border: 1px solid $color-gray;
    box-sizing: border-box;
    min-width: 400px;
    transition: .3s;
    &-layout {
      padding: 40px;
    }
  }
  .info-cart {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    position: relative;
    margin-bottom: 40px;
    transition: .3s;
    &-item {
      font-weight: 300;
      font-size: 18px;
      line-height: 130%;
      color: $color-dark;
      background-color: white;
      z-index: 9;
      &:nth-child(2){
        border-bottom: 1px dashed $color-gray;
        position: absolute;
        bottom: 5px;
        width: 100%;
        z-index: 1;
      }
      &:last-child {
        padding-left: 6px;
      }
      &.bold {
        font-weight: bold;
        font-size: 24px;
        line-height: 120%;
      }
      .opacity {
        opacity: 0.5;
      }
    }
    .woocommerce-Price-currencySymbol {
      display: none;
    }
  }
  .checkout-button {
    font-weight: bold;
    font-size: 18px;
    line-height: 130%;
    background-color: $color-dark;
    color: $color-white;
    display: block;
    text-align: center;
    transition: .3s;
    width: 100%;
    padding: 12px 0;
    margin-top: 10px;
    margin-bottom: 20px;
    &:hover {
      background-color: $color-blue;
    }
  }
  .cart-text {
    font-weight: 300;
    font-size: 14px;
    line-height: 130%;
    color: $color-dark;
    text-align: center;
    transition: .3s;
  }
}
// 
.cart {
  .product-price {
    text-align: right;
    &-label,
    &-line {
      display: none;
    }
    .product-sale {
      font-weight: 300;
      font-size: 18px;
      line-height: 130%;
      color: $color-dark;
      opacity: 0.5;
      transition: .3s;
    }
    &-total {
      font-weight: bold;
      font-size: 24px;
      line-height: 120%;
      color: $color-dark;
      transition: .3s;
    }
    .woocommerce-Price-currencySymbol {
      display: none;
    }
  }
  .product-thumbnail {
    width: 128px;
    text-align: center;
    transition: .3s;
    img {
      max-height: 50px;
      width: auto;
    }
  }
  .product-name {
    font-weight: bold;
    font-size: 24px;
    line-height: 120%;
    max-width: 171px;
    transition: .3s;
    a {
      color: $color-dark;
    }
  }
  .product-remove {
    .remove {
      position: relative;
      width: 24px;
      height: 24px;
