45ZM2.19575 2.55527L10.2934 10.6412L11.7066 9.22598L3.60894 1.14004L2.19575 2.55527ZM11.7066 10.6412L19.8043 2.55527L18.3911 1.14004L10.2934 9.22598L11.7066 10.6412ZM18.3905 2.55476L19.4452 3.60945L20.8595 2.19524L19.8048 1.14055L18.3905 2.55476Z' fill='%23B8C2C4'/%3E%3C/svg%3E%0A");
        background-size: cover;
        top: -11px;
        right: 0;
        width: 24px;
        height: 24px;
      }
    }
  }
  .product-quantity {
    display: flex;
    justify-content: space-between;
    &-label,
    &-line {
      display: none;
    }
    &-info {
      display: flex;
      justify-content: space-between;
    }
    .quantity-min {
      width: 37px;
      height: 37px;
      background-color: $color-blue;
      text-align: center;
      position: relative;
      cursor: pointer;
      &:after {
        content: '';
        position: absolute;
        width: 10px;
        height: 2px;
        left: 0;
        right: 0;
        bottom: 0;
        top: 0;
        margin: auto;
        background-image: url("data:image/svg+xml,%3Csvg width='12' height='2' viewBox='0 0 12 2' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.728 1.122C0.608 1.122 0.512 1.086 0.44 1.014C0.368 0.942 0.332 0.846 0.332 0.726V0.474C0.332 0.354 0.368 0.258 0.44 0.186C0.512 0.114 0.608 0.0779995 0.728 0.0779995H11.078C11.198 0.0779995 11.294 0.114 11.366 0.186C11.438 0.258 11.474 0.354 11.474 0.474V0.726C11.474 0.846 11.438 0.942 11.366 1.014C11.294 1.086 11.198 1.122 11.078 1.122H0.728Z' fill='white'/%3E%3C/svg%3E%0A");
      }
    }
    .quantity-max {
      width: 37px;
      height: 37px;
      background-color: $color-blue;
      text-align: center;
      position: relative;
      cursor: pointer;
      &:after {
        content: '';
        position: absolute;
        width: 13px;
        height: 13px;
        left: 0;
        right: 0;
        bottom: 0;
        top: 0;
        background-size: cover;
        margin: auto;
        background-image: url("data:image/svg+xml,%3Csvg width='11' height='11' viewBox='0 0 11 11' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5.508 10.406C5.388 10.406 5.292 10.37 5.22 10.298C5.148 10.226 5.112 10.13 5.112 10.01V6.068H1.206C1.086 6.068 0.99 6.032 0.918 5.96C0.846 5.888 0.81 5.792 0.81 5.672V5.42C0.81 5.3 0.846 5.204 0.918 5.132C0.99 5.06 1.086 5.024 1.206 5.024H5.112V1.19C5.112 1.07 5.148 0.974 5.22 0.901999C5.292 0.829999 5.388 0.793999 5.508 0.793999H5.778C5.898 0.793999 5.994 0.829999 6.066 0.901999C6.138 0.974 6.174 1.07 6.174 1.19V5.024H10.098C10.218 5.024 10.314 5.06 10.386 5.132C10.458 5.204 10.494 5.3 10.494 5.42V5.672C10.494 5.792 10.458 5.888 10.386 5.96C10.314 6.032 10.218 6.068 10.098 6.068H6.174V10.01C6.174 10.13 6.138 10.226 6.066 10.298C5.994 10.37 5.898 10.406 5.778 10.406H5.508Z' fill='white'/%3E%3C/svg%3E%0A");
      }
    }
    .quantity {
      input {
        border: 1px solid $color-blue;
        box-sizing: border-box;
        margin: 0 5px;
        height: 100%;
        text-align: center;
        width: 52px;
        font-weight: 300;
        font-size: 18px;
        line-height: 130%;
        color: $color-black;
        &::-webkit-outer-spin-button,
        &::-webkit-inner-spin-button {
          -webkit-appearance: none;
        }
      }
    }
  }
}
@media (max-width: 1200px) {
  .cart {
    .product-thumbnail {
      width: 100px;
    }
    .product-name {
      font-size: 20px;
      max-width: 100%;
    }
    .product-price-total {
      font-size: 20px;
    }
  }
}
@media (max-width: 1100px) {
  .cart-all .cart-product-item {
    flex-wrap: wrap;
  }
  .cart .product-name {
    flex-basis: 50%;
    text-align: center;
  }
}
@media (max-width: $tablet-media) {
  .cart-all {
    flex-wrap: wrap;
    .cart-product-item {
      padding: 30px 0 30px 0;
    }
  }
  .cart .product-thumbnail {
    display: none;
  }
  .cart .product-name {
    text-align: left;
    order: 1;
  }
  .cart .product-quantity {
    margin-top: 20px;
    flex-basis: 100%;
    max-width: 100%;
    order: 4;
    align-items: center;
    position: relative;
    &-label {
      display: block;
      font-weight: 300;
      font-size: 18px;
      line-height: 130%;
      color: $color-dark;
      background-color: white;
      z-index: 9;
      padding-right: 5px;
    }
    &-line {
      display: block;
      border-bottom: 1px dashed $color-gray;
      position: absolute;
      bottom: 11px;
      width: 100%;
      z-index: 1;
    }
    &-info {
      display: flex;
      background-color: $color-white;
      z-index: 9;
      padding-left: 5px;
    }
  }
  .cart .product-remove {
    order: 2;
  }
  .cart .product-price {
    margin-top: 20px;
    flex-basis: 100%;
    order: 3;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    position: relative;
    &-info {
      background-color: $color-white;
      z-index: 9;
      padding-left: 5px;
    }
    &-label {
      display: block;
      font-weight: 300;
      font-size: 18px;
      line-height: 130%;
      color: $color-dark;
      background-color: white;
      z-index: 9;
      padding-right: 5px;
    }
    &-line {
      display: block;
      border-bottom: 1px dashed $color-gray;
      position: absolute;
      bottom: 5px;
      width: 100%;
      z-index: 1;
    }
  }
  .cart-all-info {
    border: 0;
    min-width: 100%;
    &-layout {
      padding: 30px 0 0;
    }
  }
}
@media (max-width: $mobile-media) {
  .cart .product-name {
    font-size: 18px;
  }
  .cart .product-price-label,
  .cart .product-quantity-label {
    font-size: 12px;
  }
  .cart .product-price .product-sale {
    font-size: 10px;
    text-decoration: line-through;
  }
  .cart .product-price-total {
    font-size: 12px;
  }
  .cart-all .info-cart-item {
    font-weight: 500;
    font-size: 12px;
  }
  .cart-all .info-cart {
    margin-bottom: 18px;
  }
  .cart-all .info-cart-item.bold,
  .cart-all .info-cart-item .opacity {
    font-size: 12px;
  }
  .cart-all .checkout-button {
    margin-top: 24px;
  }
  .cart-all .cart-text {
    font-size: 12px;
  }
}
//  
.cart {
  .cart-collaterals {
    display: none;
  }
  .actions {
    display: none;
  }
}